body {
  min-width: 320px;
  color: #F8F8F8;
  background: #3d3d3d;
  font-family: 'Quicksand', sans-serif;
  font-size: 16px;
}

#title {
  font-size: 2.5em;
  line-height: 2em;
  margin-bottom: 10px;
}

#content {
  max-height: 100vh;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  line-height: 2em;
  padding:0 1em;
}

#options, #checks, #timer-container {
  margin-bottom: 1em;
}

#options > .timer-option {
  display: inline-block;
  width: 200px;
}

#checks > .check {
  opacity: 0.25;
}

#phase {
  font-size: 2em;
  margin-bottom: 20px;
}

#timer-container {
  display: inline-block;
  width: 240px;
  height: 240px;
  position: relative;
  border-radius: 50%;
  background: #3d3d3d;
  box-shadow: inset 15px 15px 30px #2f2f2f,
  inset -15px -15px 30px #4b4b4b;
}

#timer, #timer-canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

#timer {
  font-size: 4em;
  z-index: 1;
  width: 200px;
  height: 200px;
  line-height: 200px;
  box-sizing: border-box;
  margin-top: 20px;
  margin-left: 20px;
  border-radius: 50%;
  background: linear-gradient(145deg, #414141, #373737);
  box-shadow:  5px 5px 10px #2f2f2f,
  -5px -5px 10px #4b4b4b;
}

.timer-option {
  display: block;
}
.option-title{
  font-size: 16px;
}
.option-controller {
  display: inline-flex;
}
.click-button{
  font-size: 20px;
  font-weight: bold;
  padding-top: 3px;
  width: 2em;
  height: 2em;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 52px;
  background: linear-gradient(145deg, #3b3b3b, #3e3e3e);
  box-shadow:  6px 6px 12px #292929,
  -6px -6px 12px #4d4d4d;
}

.click-button:active{
  width: 2em;
  height: 2em;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 52px;
  background: #3b3b3b;
  box-shadow: inset 6px 6px 12px #292929,
  inset -6px -6px 12px #4d4d4d;
}
.option-controller input[type=text]{
  margin-top: 5px;
  text-align: center;
  font-size: 1em;
  width: 3em;
  height: 2em;
  color: #F8F8F8;
  background-color: #3b3b3b;
  border-radius: 5px;
}

#timer-controls{
  display: inline-flex;
}

#timer-controls  div{
  font-size: 1em;
  color: #F8F8F8;
  padding: 0.5em;
  margin-left: 25px;
  border-radius: 52px;
  background: linear-gradient(145deg, #3b3b3b, #3e3e3e);
  box-shadow:  6px 6px 12px #292929,
  -6px -6px 12px #4d4d4d;
}
#timer-controls  div:active{
  border-radius: 52px;
  background: #3b3b3b;
  box-shadow: inset 6px 6px 12px #292929,
  inset -6px -6px 12px #4d4d4d;
}
.footer {
  text-align: center;
  padding: 1rem 0 0 0;
  font-size: 1.4rem;
  color: #cccccc;
}

.footer a {
  margin-left: .5rem;
}

.footer span {
  display: inline-block;
  margin: 1rem;
}

.footer-link {
  text-decoration: none;
  color: #ffffff;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease-out;
}

.footer-link i {
  color: #ffffff;
  font-size: 1.8rem;
  margin-right: 4px;
}

.footer-link:hover, .footer-link i:hover{
  color:  #f1c40f;
}

a {
  color: #FFF;
}
/*@media screen and (min-width: 380px) and (max-width: 411px) {*/

/*}*/
@media only screen and (min-width: 320px) and (max-width: 414px) {
  #title {
    font-size: 25px;
  }
  #content{
    height: 100vh;
  }
  #options{
    margin-top: -10px;
    margin-left: -150px;
  }
  #options > .timer-option {
    display: block;
    margin: 0 auto 0.8em;
  }
  #timer-controls{
    margin-top: -560px;
    margin-left: 200px;
    display: block;
    width: 35%;
  }
  #timer-controls div{
    font-size: 14px;
    margin-bottom: 35px;
  }

  #checks{
    margin-top: .8em;
  }
  #phase{
    font-size: 1.3em;
    margin-top: -10px;
  }
  #timer-container {
    margin-top: -15px;
    width: 220px;
    height: 220px;
  }
  #timer {
    font-size: 3.5em;
    width: 180px;
    height: 180px;
    line-height: 180px;
    margin-top: 20px;
    margin-left: 20px;
  }

  .footer{
    margin-top: -50px;
  }
}
